import { createStepButton, createResetButton, step3ButtonDecorator } from "./util";

function showSelectionOptions(currentOptions) {
  if (currentOptions.length > 0) {
    currentOptions.forEach((element) => {
      const cardBlock = document.createElement("div");
      const cardBlockBody = document.createElement("div");
      cardBlock.id = `options${element.id}`;
      cardBlock.dataset.optionsBlock = element.id;
      cardBlock.className = "card mb-3";

      if (element.name) {
        const cardBlockName = document.createElement("div");
        cardBlockName.className = "card-header";
        cardBlockName.textContent = element.name;
        cardBlock.appendChild(cardBlockName);
      }

      cardBlockBody.className = "card-body";
      cardBlock.appendChild(cardBlockBody);

      element.options.forEach((option, optionIndex) => {
        const formCheck = document.createElement("div");
        formCheck.className = "form-check";
        const formCheckInput = document.createElement("input");
        formCheckInput.className = "form-check-input";

        if (!element.isradio) {
          formCheckInput.setAttribute("type", "radio");
          formCheckInput.setAttribute("name", `flexRadioDefault-${element.id}`);
          formCheckInput.setAttribute("id", `flexRadioDefault-${element.id}-${optionIndex}`);

          if (optionIndex === 0) {
            formCheckInput.setAttribute("checked", "");
          }
        } else {
          formCheckInput.setAttribute("type", "checkbox");
          formCheckInput.setAttribute("value", "");
          formCheckInput.setAttribute("id", `defaultCheck-${element.id}-${optionIndex}`);
        }

        const formCheckLabel = document.createElement("label");
        formCheckLabel.className = "form-check-label";

        if (!element.isradio) {
          formCheckLabel.setAttribute("for", `flexRadioDefault-${element.id}-${optionIndex}`);
        } else {
          formCheckLabel.setAttribute("for", `defaultCheck-${element.id}-${optionIndex}`);
        }

        formCheckLabel.innerHTML = option.replaceAll("[BR]", "<br>");
        formCheck.appendChild(formCheckInput);
        formCheck.appendChild(formCheckLabel);
        cardBlockBody.appendChild(formCheck);
      });
      document.getElementById("step2").appendChild(cardBlock);
    });
  }
}

function showCurrentTemplate(currentArea, currentTemplate) {
  const textArea = document.createElement("textarea");

  textArea.className = "form-control mb-4";
  textArea.setAttribute("id", "currenttemplate");
  textArea.setAttribute("rows", "30");
  textArea.setAttribute("readonly", "");
  textArea.value = currentTemplate.join("\n");
  currentArea.appendChild(textArea);
}

function resetState() {
  const step1Div = document.getElementById("step1");
  step1Div.style.display = "block";

  const step2Div = document.getElementById("step2");
  step2Div.innerHTML = "";
  step2Div.style.display = "none";

  const step3Div = document.getElementById("step3");
  step3Div.innerHTML = "";
  step3Div.style.display = "none";

  const step4Div = document.getElementById("step4");
  step4Div.innerHTML = "";
  step4Div.style.display = "none";
}

function showInputOptions(inputArea, inputOptions) {
  const step3Form = document.createElement("form");

  step3Form.className = "needs-validation";
  step3Form.setAttribute("id", "inputform");
  step3Form.setAttribute("novalidate", "");

  inputOptions.forEach((option, optionIndex) => {
    const mainOptionDiv = document.createElement("div");
    mainOptionDiv.className = "row align-items-center mb-3";

    const col3Div = document.createElement("div");
    col3Div.className = "col-3";

    const inputLabel = document.createElement("label");
    inputLabel.setAttribute("for", `input${optionIndex}`);
    inputLabel.textContent = option.name;

    col3Div.appendChild(inputLabel);
    mainOptionDiv.appendChild(col3Div);

    const input = document.createElement("input");
    input.className = "form-control";
    input.setAttribute("type", "text");
    input.setAttribute("id", `input${optionIndex}`);
    input.setAttribute("required", "");

    if (option.hasPlaceholder) {
      input.setAttribute("placeholder", option.placeholder);
    }

    const col3DivInput = document.createElement("div");
    col3DivInput.className = "col-3";
    col3DivInput.appendChild(input);

    const invalidDiv = document.createElement("div");
    invalidDiv.className = "invalid-feedback";
    invalidDiv.textContent = "Input required";

    col3DivInput.appendChild(invalidDiv);
    mainOptionDiv.appendChild(col3DivInput);
    step3Form.appendChild(mainOptionDiv);
  });

  const resetButton = createResetButton();
  resetButton.addEventListener("click", (e) => {
    e.preventDefault();
    resetState();
  });

  const createStep3Button = step3ButtonDecorator(createStepButton);
  const step3Button = createStep3Button("step3Button", "processinputbtn", "Process input");
  // Alternative decorator/higher-order function call without using an additional variable
  // Works well for eventlistener events, when you need to pass some context
  // console.log(step3ButtonDecorator(createStepButton)("step3Button", "processinputbtn", "Process input"));

  const col12DivInput = document.createElement("div");
  col12DivInput.className = "col-12";
  col12DivInput.appendChild(step3Button);
  col12DivInput.insertBefore(resetButton, step3Button);
  step3Form.appendChild(col12DivInput);
  inputArea.appendChild(step3Form);
}

function disableIfEmptyInput(initialBtn) {
  const initialTextArea = document.getElementById("initialtemplate");

  initialTextArea.addEventListener("input", () => {
    if (initialTextArea.value.trim().length > 0) {
      initialBtn.removeAttribute("disabled");
    } else {
      initialBtn.setAttribute("disabled", "");
    }
  });
}

function readInputOptions(currentTemplate) {
  const inputOptions = [];

  currentTemplate.forEach((element) => {
    let elementCopy = element;
    const optionRegex = /\{{2}.*\}{2}/;

    if (elementCopy.search(optionRegex) > -1) {
      while (elementCopy.search(optionRegex) > -1) {
        const singleInputOption = {};
        let currentInputOption = elementCopy.match(optionRegex);

        const doubleBracesRegex = /\{\{(?=.*)|\}\}/g;
        currentInputOption = currentInputOption[0].replace(doubleBracesRegex, "");

        if (currentInputOption.search("=") > -1) {
          singleInputOption.hasPlaceholder = true;
          currentInputOption = currentInputOption.split("=");
          currentInputOption.forEach((el, index, array) => {
            array[index] = el.trim();
          });
          [singleInputOption.name, singleInputOption.placeholder] = currentInputOption;
        } else {
          singleInputOption.hasPlaceholder = false;
          singleInputOption.name = currentInputOption;
        }

        inputOptions.push(singleInputOption);
        elementCopy = elementCopy.replace(optionRegex, "");
      }
    }
  });
  return inputOptions;
}

function readSelectionOptions(currentOptions) {
  currentOptions.forEach((element, index) => {
    /* eslint no-param-reassign: ["error", { "props": false }] */
    element.optionselection = [];
    for (let optionIndex = 0; optionIndex < element.options.length; optionIndex += 1) {
      if (!element.isradio) {
        if (document.getElementById(`flexRadioDefault-${element.id}-${optionIndex}`).checked) {
          currentOptions[index].optionselection.push(element.options[optionIndex]);
          return;
        }
      } else if (document.getElementById(`defaultCheck-${element.id}-${optionIndex}`).checked) {
        currentOptions[index].optionselection.push(element.options[optionIndex]);
      }
    }
  });
}

function readUserInputOptions(inputOptions) {
  inputOptions.forEach((option, optionIndex) => {
    // eslint-disable-next-line no-param-reassign
    option.input = document.getElementById(`input${optionIndex}`).value;
  });
}

function createStepHeader(divName, headerText, areaName, headerName) {
  let innerAreaName = areaName;
  let innerHeaderName = headerName;

  innerAreaName = document.getElementById(divName);
  innerHeaderName = document.createElement("h5");
  innerAreaName.style.display = "block";
  innerHeaderName.textContent = headerText;
  innerAreaName.appendChild(innerHeaderName);

  return innerAreaName;
}

export { showSelectionOptions, showCurrentTemplate, showInputOptions, resetState, disableIfEmptyInput, readInputOptions, readSelectionOptions, readUserInputOptions, createStepHeader };
