function deleteOptionName(optionName, string) {
  const bracedOptionName = `{${optionName}}`;

  return string.replace(bracedOptionName, "");
}

function isOneLineOption(currentLine) {
  if (currentLine.includes("]]")) {
    const openingBracketMatchCount = currentLine.match(/\[{2}/g) || "";
    const closingBracketMatchCount = currentLine.match(/\]{2}/g);

    if (openingBracketMatchCount.length === closingBracketMatchCount.length) {
      return true;
    }
  }

  return false;
}

function isNamed(string) {
  const firstOpenBracketIndex = string.indexOf("[[");
  const afterOpenBracketSymbol = string.substring(firstOpenBracketIndex + 2, firstOpenBracketIndex + 3);

  if (afterOpenBracketSymbol === "{") {
    return true;
  }

  return false;
}

function isRadio(string) {
  const firstOpenBracketIndex = string.indexOf("[[");
  const afterOpenBracketSymbols = string.substring(firstOpenBracketIndex + 2, firstOpenBracketIndex + 4);

  if (afterOpenBracketSymbols === "**") {
    return true;
  }

  return false;
}

function getOptionName(string) {
  const regex = /(?<=\{{1}).*?(?=\}{1})/;
  const optionsName = string.match(regex);

  return optionsName[0];
}

function getOption(currentLine, arr, lineIdx, endLineIdx, optional) {
  let options = [];

  if (endLineIdx === lineIdx) {
    const regex = /(?<=\[{2}).*?(?=\]{2})/;
    const optionsString = currentLine.match(regex);
    options = optionsString[0].split("|");
  } else {
    const firstOpenBracketIndex = currentLine.indexOf("[[");
    const firstCloseBracketIndex = arr[endLineIdx].indexOf("]]");
    const firstLine = currentLine.substring(firstOpenBracketIndex + 2);
    const lastLine = arr[endLineIdx].substring(0, firstCloseBracketIndex);
    let optionsString = "";
    optionsString = `${firstLine}[BR]`;

    for (let m = lineIdx + 1; m < endLineIdx; m += 1) {
      optionsString += `${arr[m]}[BR]`;
      options = optionsString;
    }
    optionsString += lastLine;

    if (!optional) {
      options = optionsString.split("|");
    } else {
      options = optionsString.split("**");
      options.shift();
    }
  }

  return options;
}

function replaceOptions(currentLine, optionID, arr, lineIdx, endLineIdx) {
  let optionsString = null;
  const replacement = `[OPTION${optionID}]`;

  if (endLineIdx === lineIdx) {
    const regex = /(?<=\[{2}).*?(?=\]{2})/;
    optionsString = currentLine.replace(regex, replacement);
  } else {
    const firstOpenBracketIndex = arr[lineIdx].indexOf("[[");
    const firstCloseBracketIndex = arr[endLineIdx].indexOf("]]");
    const firstSubstringToReplace = arr[lineIdx].substring(firstOpenBracketIndex + 2);
    const lastSubstringToReplace = arr[endLineIdx].substring(0, firstCloseBracketIndex);
    const firstCleanLine = arr[lineIdx].replace(firstSubstringToReplace, replacement);
    const lastCleanLine = arr[endLineIdx].replace(lastSubstringToReplace, "");

    optionsString = firstCleanLine + lastCleanLine;
  }
  return optionsString;
}

function trimArray(arr) {
  arr.forEach((item, index, array) => {
    // eslint-disable-next-line no-param-reassign
    array[index] = item.trim();
  });

  return arr;
}

function createStepButton(buttonName, buttonId, buttonText) {
  let button = buttonName;
  const currentButtonId = buttonId;

  button = document.createElement("button");
  button.id = currentButtonId;
  button.type = "button";
  button.className = "float-end btn btn-outline-primary btn-lg";
  button.textContent = buttonText;

  return button;
}

// Decorator/higher-order function example
function step3ButtonDecorator(func) {
  return function originalFunction(...args) {
    const result = func.call(null, ...args);
    result.type = "submit";

    return result;
  };
}

function nonFloatButtonDecorator(func) {
  return function originalFunction(...args) {
    const result = func.call(null, ...args);
    result.className = "btn btn-outline-primary btn-lg ms-3";

    return result;
  };
}

function getProgressBar(readyness) {
  const outerDiv = document.createElement("div");
  outerDiv.className = "progress mb-3";
  outerDiv.setAttribute("role", "progressbar");
  outerDiv.setAttribute("aria-label", "Template readyness level");
  outerDiv.setAttribute("aria-valuenow", readyness);
  outerDiv.setAttribute("aria-valuemin", "0");
  outerDiv.setAttribute("aria-valuemax", "100");
  outerDiv.setAttribute("style", "height: 20px");

  const innerDiv = document.createElement("div");
  innerDiv.className = "progress-bar progress-bar-striped bg-success";
  innerDiv.setAttribute("style", `width: ${readyness}%`);
  innerDiv.textContent = `${readyness}%`;

  outerDiv.appendChild(innerDiv);

  return outerDiv;
}

function initialParse(array) {
  const selectionOptionsArr = [];
  let id = null;
  let endLineIndex = null;
  const processedTemplate = [];

  for (let lineIndex = 0; lineIndex < array.length; lineIndex += 1) {
    let currentLine = array[lineIndex];
    if (currentLine.includes("[[")) {
      const currentOption = {};
      id += 1;

      if (isNamed(currentLine)) {
        currentOption.name = getOptionName(currentLine);
        currentLine = deleteOptionName(currentOption.name, currentLine);
      }

      currentOption.isradio = isRadio(currentLine);

      if (isOneLineOption(currentLine)) {
        endLineIndex = lineIndex;
        currentOption.options = getOption(currentLine, array, lineIndex, endLineIndex, currentOption.isradio);
        currentOption.id = id;
        currentOption.multiline = false;
        currentLine = replaceOptions(currentLine, currentOption.id, array, lineIndex, endLineIndex);
      } else {
        const initialLineIndex = lineIndex;

        endLineIndex = null;
        for (let newLineIndex = lineIndex; newLineIndex < array.length; newLineIndex += 1) {
          if (deleteOptionName(currentOption.name, array[newLineIndex]).includes("]]") && !isOneLineOption(array[newLineIndex])) {
            endLineIndex = newLineIndex;
            lineIndex = newLineIndex;
            break;
          }
        }

        if (endLineIndex === null) {
          throw new Error("Can't find the closing bracket. You probably have unmatching brackets in the template.");
        }

        currentOption.options = getOption(currentLine, array, initialLineIndex, endLineIndex, currentOption.isradio);
        currentOption.id = id;
        currentOption.multiline = true;
        currentLine = replaceOptions(currentLine, currentOption.id, array, initialLineIndex, endLineIndex);
        lineIndex = endLineIndex;
      }
      selectionOptionsArr.push(currentOption);
    }
    processedTemplate.push(currentLine);
  }

  return [processedTemplate, selectionOptionsArr];
}

function createResetButton() {
  const makeResetButton = nonFloatButtonDecorator(createStepButton);
  const resetButton = makeResetButton("resetButton", "resetbtn", "Start over");
  return resetButton;
}

function updateSelectionTemplate(currentTemplate, currentOptions) {
  const selectedTemplate = [];
  let processedOption = 1;

  currentTemplate.forEach((element, index) => {
    if (processedOption <= currentOptions.length && element.search(`[[[OPTION${processedOption}]]]`) > -1) {
      let replacedElement;

      while (element.search(`[[[OPTION${processedOption}]]]`) > -1) {
        const replacement = currentOptions[processedOption - 1].optionselection.join("");
        replacedElement = element.replace(`[[[OPTION${processedOption}]]]`, replacement);
        processedOption += 1;
      }

      const readyOptions = replacedElement.split("[BR]");
      readyOptions.forEach((option) => selectedTemplate.push(option));
    } else {
      selectedTemplate.push(currentTemplate[index]);
    }
  });

  return selectedTemplate;
}

function replaceInputOptions(currentTemplate, inputOptions) {
  const replacedTemplate = [];
  let optionIndex = 0;
  currentTemplate.forEach((templateString) => {
    const optionRegex = /\{\{.*\}\}/;
    let stringCopy = templateString;

    if (stringCopy.search(optionRegex) > -1) {
      while (stringCopy.search(optionRegex) > -1) {
        stringCopy = stringCopy.replace(optionRegex, inputOptions[optionIndex].input);
        optionIndex += 1;
      }
    }

    replacedTemplate.push(stringCopy);
  });
  return replacedTemplate;
}

export { deleteOptionName, isOneLineOption, isNamed, isRadio, getOptionName, getOption, replaceOptions, trimArray, createStepButton, step3ButtonDecorator, nonFloatButtonDecorator, getProgressBar, initialParse, createResetButton, updateSelectionTemplate, replaceInputOptions };
