import { trimArray, initialParse, getProgressBar, createStepButton, createResetButton, updateSelectionTemplate, replaceInputOptions, nonFloatButtonDecorator } from "./util";
import { showCurrentTemplate, showSelectionOptions, showInputOptions, resetState, readSelectionOptions, readInputOptions, readUserInputOptions, createStepHeader } from "./dom";

function step4Init(finalTemplate) {
  const progressBar = getProgressBar(100);
  const step3Div = document.getElementById("step4");
  step3Div.appendChild(progressBar);

  const step4Area = createStepHeader("step4", "Final template", "step4Area", "step4Header");
  showCurrentTemplate(step4Area, finalTemplate);

  const createStep4Button = nonFloatButtonDecorator(createStepButton);
  const copyClipboardButton = createStep4Button("copyClipBoardBtn", "copybtn", "Copy to clipboard");
  const step4Button = createStep4Button("step4Button", "startoverbtn", "Start over");

  const floatEndDiv = document.createElement("div");
  floatEndDiv.className = "float-end";
  floatEndDiv.appendChild(copyClipboardButton);
  floatEndDiv.appendChild(step4Button);
  step4Area.appendChild(floatEndDiv);

  copyClipboardButton.addEventListener("click", (event) => {
    event.preventDefault();
    const textArea = document.getElementById("currenttemplate");
    const clipboardCopy = navigator.clipboard.writeText(textArea.value);
    clipboardCopy.catch((error) => {
      // eslint-disable-next-line no-console
      console.error(error);
    });
  });

  step4Button.addEventListener("click", (event) => {
    event.preventDefault();
    resetState();
  });
}

function step3Init(step3Template) {
  const progressBar = getProgressBar(66);
  const step3Div = document.getElementById("step3");
  step3Div.appendChild(progressBar);

  const step3Area = createStepHeader("step3", "Current template state", "step3Area", "step3Header");

  showCurrentTemplate(step3Area, step3Template);

  const inputOptions = readInputOptions(step3Template);
  const innerHeader = document.createElement("h5");

  innerHeader.textContent = "Input Values";
  step3Area.appendChild(innerHeader);

  showInputOptions(step3Area, inputOptions);

  let readyStep3Template = [];
  const step3Btn = document.getElementById("processinputbtn");

  step3Btn.addEventListener("click", (event) => {
    const form = document.getElementById("inputform");

    if (!form.checkValidity()) {
      event.preventDefault();
      event.stopPropagation();
      form.classList.add("was-validated");
    } else {
      event.preventDefault();
      readUserInputOptions(inputOptions);
      readyStep3Template = replaceInputOptions(step3Template, inputOptions);
      step3Area.innerHTML = "";
      step3Area.style.display = "none";

      step4Init(readyStep3Template);
    }
  });
}

function step2Init(template, selectionOptions) {
  const progressBar = getProgressBar(33);
  const step2Div = document.getElementById("step2");
  step2Div.appendChild(progressBar);

  const step2Area = createStepHeader("step2", "Select options", "step2Area", "step2Header");

  showSelectionOptions(selectionOptions);

  step2Area.appendChild(createStepButton("step2Button", "selectionbtn", "Process selections"));

  const resetButton = createResetButton();
  resetButton.addEventListener("click", (e) => {
    e.preventDefault();
    resetState();
  });

  const step2Button = document.getElementById("selectionbtn");

  step2Area.insertBefore(resetButton, step2Button);

  step2Button.addEventListener("click", (event) => {
    event.preventDefault();

    readSelectionOptions(selectionOptions);

    const processedTemplate = updateSelectionTemplate(template, selectionOptions);

    step2Area.innerHTML = "";
    step2Area.style.display = "none";

    step3Init(processedTemplate);
  });
}

function step1Init(initialTextArea, buttonElem) {
  const initialTextarea = document.getElementById("initialtemplate");
  const savedTemplate = JSON.parse(localStorage.getItem("template"));
  if (savedTemplate) {
    initialTextarea.value = savedTemplate;
    buttonElem.removeAttribute("disabled");
  }

  const step1Area = document.getElementById("step1");
  let processedTemplate;
  let selectionOptionsArr;

  buttonElem.addEventListener("click", (e) => {
    e.preventDefault();

    localStorage.setItem("template", JSON.stringify(initialTextarea.value));

    const templateInit = trimArray(initialTextArea.value.split("\n"));
    [processedTemplate, selectionOptionsArr] = initialParse(templateInit);

    step1Area.style.display = "none";

    step2Init(processedTemplate, selectionOptionsArr);
  });
}

export { step1Init, step2Init, step3Init, step4Init };
